import React from 'react'
import {
  Box,
  TextField,
  Typography,
  Grid,
  Button,
  Paper,
  IconButton,
  MenuItem,
  Breadcrumbs,
  Link
} from '@mui/material'
import SaveIcon from '@mui/icons-material/Save'
import PersonIcon from '@mui/icons-material/Person'
import HomeIcon from '@mui/icons-material/Home'
import SettingsIcon from '@mui/icons-material/Settings'
import InputMask from 'react-input-mask'

const UserSettings = () => {
  const provincias = [
    "Buenos Aires",
    "Catamarca",
    "Chaco",
    "Chubut",
    "Córdoba",
    "Corrientes",
    "Entre Ríos",
    "Formosa",
    "Jujuy",
    "La Pampa",
    "La Rioja",
    "Mendoza",
    "Misiones",
    "Neuquén",
    "Río Negro",
    "Salta",
    "San Juan",
    "San Luis",
    "Santa Cruz",
    "Santa Fe",
    "Santiago del Estero",
    "Tierra del Fuego",
    "Tucumán",
  ]

  return (
    <Box sx={{ padding: 6 }}>
      <Breadcrumbs aria-label="breadcrumb" sx={{ marginBottom: 2 }}>
        <Link href="/home" color="inherit" underline="hover">Inicio</Link>
        <Typography color="textPrimary">Configuración de usuario</Typography>
      </Breadcrumbs>

      <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', marginBottom: 4 }}>
        <Box sx={{ display: 'flex', alignItems: 'center' }}>
          <SettingsIcon sx={{ marginRight: 1, fontSize: 40, color: '#3F51B5' }} />
          <Typography variant="h4" gutterBottom sx={{
            color: '#3F51B5',
            fontFamily: 'Roboto, sans-serif',
            fontWeight: 700,
            textAlign: 'center',
            fontSize: { xs: 20, md: 30 },
            my: 0
           }}>
            Configuración de Usuario
          </Typography>
        </Box>
        <IconButton color="primary" aria-label="guardar cambios" sx={{ fontSize: 32 }}>
          <SaveIcon fontSize="inherit" />
        </IconButton>
      </Box>

      {/* Información Personal */}
      <Paper elevation={3} sx={{ padding: 4, marginBottom: 6 }}>
        <Box sx={{ display: 'flex', alignItems: 'center', marginBottom: 2 }}>
          <PersonIcon sx={{ marginRight: 1 }} />
          <Typography variant="h6" gutterBottom my={0}>
            Información Personal
          </Typography>
        </Box>
        <Grid container spacing={3}>
          <Grid item xs={12} sm={6}>
            <TextField fullWidth label="Nombre" variant="outlined" />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField fullWidth label="Apellido" variant="outlined" />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField fullWidth label="Nombre de Usuario" variant="outlined" />
          </Grid>
          <Grid item xs={12} sm={6}>
            <InputMask mask="(99) 9999-9999" maskChar=" ">
              {(inputProps) => (
                <TextField {...inputProps} fullWidth label="Teléfono" variant="outlined" />
              )}
            </InputMask>
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField fullWidth label="Email" variant="outlined" />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField fullWidth label="Confirmar Email" variant="outlined" />
          </Grid>
        </Grid>
      </Paper>

      {/* Dirección */}
      <Paper elevation={3} sx={{ padding: 4 }}>
        <Box sx={{ display: 'flex', alignItems: 'center', marginBottom: 2 }}>
          <HomeIcon sx={{ marginRight: 1 }} />
          <Typography variant="h6" gutterBottom my={0}>
            Dirección
          </Typography>
        </Box>
        <Grid container spacing={3}>
          <Grid item xs={12}>
            <TextField fullWidth label="Dirección" variant="outlined" />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              fullWidth
              select
              label="Provincia"
              variant="outlined"
            >
              {provincias.map((provincia, index) => (
                <MenuItem key={index} value={provincia}>
                  {provincia}
                </MenuItem>
              ))}
            </TextField>
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField fullWidth label="Calle" variant="outlined" />
          </Grid>
          <Grid item xs={12} sm={6}>
            <InputMask mask="9999" maskChar=" ">
              {(inputProps) => (
                <TextField {...inputProps} fullWidth label="Número" variant="outlined" />
              )}
            </InputMask>
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField fullWidth label="Departamento" variant="outlined" />
          </Grid>
          <Grid item xs={12}>
            <TextField fullWidth label="Entre Calles" variant="outlined" />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField fullWidth label="Barrio" variant="outlined" />
          </Grid>
          <Grid item xs={12} sm={6}>
            <InputMask mask="9999" maskChar=" ">
              {(inputProps) => (
                <TextField {...inputProps} fullWidth label="Código Postal" variant="outlined" />
              )}
            </InputMask>
          </Grid>
        </Grid>
      </Paper>

      <Box sx={{ marginTop: 4, textAlign: 'center' }}>
  <Button
    variant="contained"
    color="error"
    startIcon={<PersonIcon />}
    sx={{ padding: '10px 20px', fontWeight: 'bold' }}
  >
    Solicitar la baja de mi cuenta
  </Button>
</Box>

    </Box>
  )
}

export default UserSettings
